
import { createWebHistory, createRouter } from "vue-router";
import Home from './comHome.vue';
import About from './comAbout.vue';
import Skill from './comSkill.vue';
import Portfolio from './comPortfolio.vue';
import Contact from './comContact.vue';

const routes = [
   {
      path: "/",
      component: Home,
    },
    {
      path: "/about",
      component: About,
    },
    {
      path: "/skill",
      component: Skill,
    },
    {
      path: "/portfolio",
      component: Portfolio,
     },
    {
      path: "/contact",
      component: Contact,
    }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 